.wrapper {
    width: 420px;
    background: transparent;
    border: 2px solid rgba(255,255,255,.2);
    /* background: slateblue; */
    /* background-color: rgba(106, 90, 205, 0.5); */
    color: #ffff;
    border-radius: 10px;
    padding: 30px 40px;
}

.wrapper h1 {
    font-family: Lobster;
    font-size: 54px;
    text-align: center;
}

.wrapper h2 {
    font-family: Lobster;
    font-size: 36px;
    text-align: center;
}

.wrapper .input-box {
    position: relative;
    width: 100%;
    height: 50px;
    margin: 30px 0;
}

.input-box input {
    width: 100%;
    height: 100%;
    background: transparent;
    border: 2px solid rgba(255,255,255, .2);
    outline: none;
    border-radius: 40px;
    font-size: 16px;
    color: #ffff;
    padding: 20px 45px 20px 20px;
}

.input-box input::placeholder {
    color: #ffff;
}

.input-box .icon {
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 16px;
}

.wrapper .remember-forgot {
    display: flex;
    justify-content: space-between;
    font-size: 14.5px;
    margin: -15px 0 15px;
}

.remember-forgot label input {
    accent-color: #ffff;
    margin-right: 4px;
}

.remember-forgot a {
    color: #ffff;
    text-decoration: none;
}

.remember-forgot a:hover {
    text-decoration: underline;
}

.wrapper button {
    width: 100%;
    height: 45px;
    background: #ffff;
    border: none;
    outline: none;
    border-radius: 40px;
    /* box-shadow: 0 0 10px rgba(0,0,0,1); */
    cursor: pointer;
    font-size: 16px;
    color: #333;
    font-weight: 700;
}

.wrapper .register-link {
    font-size: 14.5px;
    text-align: center;
    margin: 20px 0 15px;
}

.register-link p a {
    color: #ffff;
    text-decoration: none;
    font-weight: 600;
}

.register-link p a:hover {
    text-decoration: underline;
}

.wrapper .existingAccount-link {
    font-size: 14.5px;
    text-align: center;
    margin: 20px 0 15px;
}

.existingAccount-link p a {
    color: #ffff;
    text-decoration: none;
    font-weight: 600;
}

.existingAccount-link p a:hover {
    text-decoration: underline;
}